import back from '../../components/back.vue';
export default {
	data() {
		return {
			phoneNum:'',
			phoneNumTrue:'',
			pageNameFlag:1,
			showFlag:0,
			pageName:'账号绑定手机号更换',
			text:'验证码将发送到绑定手机号',
			vercode:'',
			resetFlag:0,
			nextFlag:0,
			sendAgain:'发送验证码',
			sendAgainFlag:0,
			second:60,
			errorMsg:'',
			loadingFlag:false,
			disabledFlag:false
		}
	},
	methods: {
		changeVercode(e){
			var that = this;
			if(e.detail.value.length>0){
				that.vercode=e.detail.value;
				that.resetFlag=1;
			}else{
				that.vercode='';
				that.resetFlag=0;
			}
			if(e.detail.value.length==6){
				that.nextFlag=1
			}else{
				that.nextFlag=0
			}
		},
		sendVercodeAgain(){
			var that = this;
			if(that.judgeLogin()){
				that.sendAgainFlag=1
				uni.request({
				    url: that.serverUrl+'/user/authCode', 
					method:'POST',
				    data: {
				       phone:uni.getStorageSync('userInfo').phone,
					   devCode:that.cid(),
					   type:5
				    },
				    header: {
				        'content-type': 'application/x-www-form-urlencoded'
				    },
					sslVerify:false,
				    success: (res) => {
						if(res.data.resultCode=='10000'){
								
						}else{
							that.errorMsg=that.showError(res.data.msgCode,res.data.msg);
							that.showFlag=1
							setTimeout(function() {
								that.errorMsg='';
								that.showFlag=0;
							}, 5000);
						}
				    },
					fail() {
						uni.showModal({
						    title: '提示',
						    content: '网络异常，请检查网络后重试',
							showCancel:false,
						    success: function (res) {
						        if (res.confirm) {
						            console.log('用户点击确定');
						        } else if (res.cancel) {
						            console.log('用户点击取消');
						        }
						    }
						});
					}
				});
				if(that.second!=60){
					return;
				}
				that.second--;
				var intervalID =setInterval(function(){
					that.sendAgain ='重新发送('+that.second-- +"S)";
				},1000);
				setTimeout(function(){
					clearInterval(intervalID);
					that.sendAgain ="发送验证码";
					that.second=60;
					that.sendAgainFlag=0
				},61000)
			}	
		},
		reset(){
			this.vercode='';
			this.resetFlag=0;
			this.nextFlag=0
		},
		nextPage(){
			var that = this;
			if(that.judgeLogin()){
				that.sendAgainFlag=1;
				that.disabledFlag=true;
				that.loadingFlag=true;
				uni.request({
				    url: that.serverUrl+'/user/comfirmCode', 
					method:'POST',
				    data: {
				       phone:uni.getStorageSync('userInfo').phone,
					   devCode:that.cid(),
					   code:that.vercode
				    },
				    header: {
				        'content-type': 'application/x-www-form-urlencoded'
				    },
					sslVerify:false,
				    success: (res) => {
						that.disabledFlag=false;
						that.loadingFlag=false;
						if(res.data.resultCode=='10000'){
								uni.navigateTo({
									url:'../modifyUserPhoneNumSetPhoneNum/modifyUserPhoneNumSetPhoneNum'
								})
						}else{
							that.errorMsg=that.showError(res.data.msgCode,res.data.msg);
							that.showFlag=1
							setTimeout(function() {
								that.errorMsg='';
								that.showFlag=0;
							}, 5000);
						}
				    },
					fail() {
						that.disabledFlag=false;
						that.loadingFlag=false;
						uni.showModal({
						    title: '提示',
						    content: '网络异常，请检查网络后重试',
							showCancel:false,
						    success: function (res) {
						        if (res.confirm) {
						            console.log('用户点击确定');
						        } else if (res.cancel) {
						            console.log('用户点击取消');
						        }
						    }
						});
					}
				});
			}	
		}
	},
	onLoad(e) {
		this.phoneNum = e.phoneNum.substr(0,3)+'****'+e.phoneNum.substr(7,11);
	},
	components:{
		back
	}
}
